.recipe {
  background: rgb(210, 164, 216);
  border-radius: 10px;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.6);
  margin: 1em;
  padding: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
}

img {
  border-radius: 50%;
  padding: 1em;
}

ol {
  list-style-type: none;
}

ol li {
  padding-bottom: 0.6em;
}
