body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  background-image: linear-gradient(to right, #c27be0, #d761dd);
  min-height: 100vh;
}

.search-form {
  min-height: 13vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 0.6em;
  border-radius: 3px;
  margin-right: 0.6em;
}

.search-button {
  background: rgb(210, 164, 216);
  color: white;
  letter-spacing: 2px;
  border: none;
  border-radius: 3px;
  padding: 0.6em 1.2em;
}

.recipes {
  display: flex;
  flex-direction: column;
  color: black;
}
